*{
  margin: 0px;
  background-color:#484848;
  font-family: 'Abel', sans-serif;
}

.name{
    color: black;
    font-size: calc(6px + 2vmin);
    display: flex;
    justify-content: center;
    padding-top: 30px;
    padding-bottom: 30px;
    color: #fff;
    /* animation: flicker 1.5s infinite alternate; */
}
.header{    
    padding-top: 20px;
}
.profilepicwrapper{
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
}
.profilepic{
    border: 5px solid rgb(255, 255, 255);
    height: 100px !important;
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
}
.header__info{
    padding-left: 10px;
    text-align: left;
    width: 60%;    
}
.profileflex{
    display: flex;
}
.Contact_Details{
    text-align: right;
    display: flex;
    flex-direction: column;
}
.educationflex__flex{
    display: flex;
    justify-content: space-between;
    font-size: 11px;
    color: rgb(240, 238, 238);
    font-style: italic;
    font-weight: 400 !important;
    width: 70%;
}
.educationlevel{
    font-weight: bold;
}
.pskills{
    display: flex;    
}
.project__desc{
    
}
.workflex h4{
    font-weight: bold;
}
.pskills__text{
    font-size: 15px;
    margin-left: 5px;
    border-radius: 8px;
    margin-top: 5px;
    padding: 5px;
    vertical-align: middle;
    text-align: center;
    color: white;
    background-color: rgb(0, 0, 0);
}
.links{
    /* display: flex; */
    text-align: right;
}
.circle__notblank{
    margin-left: 20px;  
    height: 15px;
    width: 15px;
    background-color: rgb(0, 0, 0);
    border-radius: 50%;      
  }
.circle__blank{
  border: 2px solid rgb(0, 0, 0);
  margin-left: 20px;  
  height: 15px;
  width: 15px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;      
}
.progress{
    height: 30px;
}
.seperater{
    width: 100%;
    height: 1px;
    background-color: antiquewhite;
}
.links  a{
    text-decoration: none !important;
    color: black;
    padding-left: 20px;
}
.educationflex{
    display: flex;
    flex-direction: column;
    padding-top: 30px;
}
.Contact{
    display: flex;
    justify-content: center;
}
.circle_flex{
    padding-top: 10px;
    display: flex;
    justify-content: center;
}
.def{
    text-align: center;
}
.skills__image{   
    height: 30px !important;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.skills{
    display: flex;
    flex-direction: column;
}
.skills__flex{
    margin-top: 20px;
    display: flex;
    flex-direction: row;
}
.imgcol{
    width: 100px !important;
}
.workflex{
    display: flex;
    flex-direction: column;
}
.projects__flex{
    display: flex;
    flex-direction: column;
}
.interests{
    display: flex;
}



@keyframes flicker {
    
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
  
        text-shadow:
        0 0 1px #fff,
        0 0 1px #fff,
        0 0 0px #fff,
        0 0 19px rgb(0, 47, 255),
        0 0 19px rgb(0, 47, 255),
        0 0 19px rgb(0, 47, 255),
        0 0 19px rgb(0, 47, 255),
        0 0 19px rgb(0, 47, 255);
    
    }
    
    20%, 24%, 55% {        
        text-shadow: none;
    }    
  }
  h2{
      padding-top: 30px;
      color: white;
  }

  .Detail{
      padding-bottom: 100px;
  }
  @media screen and (max-width: 425px) {
    .skills__image{
        margin-top: 20px;
        height: 35px !important;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
  }
  
